const qcloudApi = container.resolve<IQCloudApi>(IQCloudApi);
const viewModel = container.resolve<IViewModel>(IViewModel);
const showingTab = ref(<"publish" | "download">("publish"));
const tableData = ref<Array<PublishHistoryTableRow>>();
const tableHeaders = [
  { name: "Title", style: "padding-left:20px" },
  { name: "Create Date", style: "" },
  { name: "Created By", style: "" },
  { name: "Status", style: "" },
  { name: "Platform", style: "" },
];
const artifactListVisible = ref(false)
const artifactsMap = reactive(new Map<string, Array<IExportJobArtifactInfoItem>>());

