  getApplicationJobHistory();
})


const getApplicationJobHistory = async () => {
  executor.execute(
    async () => {
      let historyList: Array<IExportJobHistoryDbItem> = await qcloudApi.listApplicationJobHistory({ applicationID: viewModel.studio.appId, type: showingTab.value });
      let openedRows: Map<string, Array<Step>> = new Map();

      let currentlyOpenedRows = tableData.value?.filter(e => e.showInformation);
      if (currentlyOpenedRows)
        for await (const e of currentlyOpenedRows) {
          let steps = await getListExportJobSteps(e.title, e.isMultiStepJob);
          openedRows.set(e.title, steps)
        }

      tableData.value = historyList.map(e => {
        const isRowOpened = openedRows.has(e.ID);

        return {
          title: e.ID,
          createDate: dateFormat(new Date(e.createDate)),
          createdBy: e.createdBy,
          status: e.status,
          platform: e.status == "success" ? e.platform : undefined,
          steps: isRowOpened ? openedRows.get(e.ID) : [],
          showInformation: isRowOpened,
          isMultiStepJob: e.isMultiStep,
          cloudProvider: e.cloudProvider
        } as PublishHistoryTableRow
      })
    },
    { loading: true }
  );
}

getApplicationJobHistory();

function dateFormat(date: Date) {
  return `${date.toLocaleDateString("en-US", {day: "2-digit", month: "2-digit", year: "numeric"}).replace(/\//g, ".")}, ${date.toLocaleString("en-US", {second: "2-digit", minute: "2-digit", hour: "2-digit", hourCycle: "h24"})}`;
}

async function openCloseRow(row: PublishHistoryTableRow) {
  if (row.status == "waiting")
    return

  if (row.showInformation) {
    row.showInformation = false;
    return;
  }

  executor.execute(
    async () => {
      row.steps = await getListExportJobSteps(row.title, row.isMultiStepJob);
      if (showingTab.value == "download" && row.status == "success") {
        await getArtifacts(row);
      }
    },
    { loading: true }
  );

  row.showInformation = !row.showInformation;
}

async function getListExportJobSteps(ID: string, isMultiStep: boolean) {
  let jobSteps = await qcloudApi.listExportJobSteps(ID, isMultiStep);
  return jobSteps.map(e => {
    return {
      ID: e.ID,
      name: e.name,
      status: e.status
    } as Step
  });
}

async function refreshTable() {
  await getApplicationJobHistory();
}

// download artifacts
async function getArtifacts(row: PublishHistoryTableRow) {
  if (!artifactsMap.has(row.title)) {
    const artifacts = await qcloudApi.listExportJobArtifacts(row.title);
    if (artifacts && artifacts.length > 0) {
      artifactsMap.set(row.title, artifacts);
    }
  }

}

function computedArtifact(type: string) {
  switch (type) {
    case 'web':
      return 'mdi mdi-monitor artifactIcons';
    case 'ios':
      return 'mdi mdi-apple-ios artifactIcons';
    case 'android':
      return 'mdi mdi-android artifactIcons';
    default:
      break;
  }
}

function getArtifactLink(artifact: IExportJobArtifactInfoItem) {
  const token = viewModel.user!.authorization;
  const { objectName } = artifact.details;
  const url = config.getValue("providerApiUrl");
  return network.join(url, `/minio/getExportJobArtifact?objectName=${objectName}&t=${token}`);
}


function downloadArtifact(artifact: IExportJobArtifactInfoItem) {
  const a = document.createElement("a");
  a.href = getArtifactLink(artifact);
  a.target = "new";
  a.download = "download";
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
}

async function showArtifactsBtnClicked(row: PublishHistoryTableRow) {
  await getArtifacts(row);
  artifactListVisible.value = !artifactListVisible.value;
}

