___CSS_LOADER_EXPORT___.push([module.id, "\n.content-header[data-v-f3ef42d0] {\n  /* Layout Properties */\n  display: flex;\n  margin-top: 96px;\n  margin-left: 252px;\n  width: 1417px;\n  height: 120px;\n  /* UI Properties */\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  border: 1px solid #BCBCBC;\n  border-radius: 4px;\n  opacity: 1;\n}\n.tabs[data-v-f3ef42d0] {\n  margin-top: 12px;\n  margin-left: 252px;\n  width: 1417px;\n  height: 828px;\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  border: 1px solid #BCBCBC;\n  border-radius: 4px;\n  opacity: 1;\n}\n.prev-button[data-v-f3ef42d0] {\n  margin-left: 17px;\n  opacity: 1;\n  cursor: pointer;\n}\n.app-name[data-v-f3ef42d0] {\n  margin-left: 9px;\n  text-align: left;\n  font: normal normal normal 15px/20px Roboto;\n  letter-spacing: 0px;\n  color: #616161;\n  opacity: 1;\n}\n.progress-details-text[data-v-f3ef42d0] {\n  /* UI Properties */\n  padding-left: 12px;\n  font-style: normal;\n  font-weight: 500;\n  ;\n  font-family: Roboto;\n  letter-spacing: 0;\n  color: #212121;\n  text-align: left;\n  opacity: 1;\n}\n\n/* tab */\n.bar[data-v-f3ef42d0] {\n  margin-top: 32px;\n  margin-left: 32px;\n  margin-bottom: 20px;\n}\n.bar input[type=\"radio\"][data-v-f3ef42d0] {\n  opacity: 0;\n  position: fixed;\n  width: 93px;\n  height: 21px;\n  cursor: pointer;\n}\n.bar label[data-v-f3ef42d0] {\n  text-align: left;\n  font: normal normal medium 16px/21px Roboto;\n  font-weight: 500;\n  letter-spacing: 0px;\n  color: #A1A1A1;\n  opacity: 1;\n  margin-right: 28px;\n}\n.bar input[type=\"radio\"]:checked+label[data-v-f3ef42d0] {\n  color: #151233;\n  border-bottom: solid;\n  border-bottom-color: #151233;\n}\n\n/* tab content */\n.tab-content[data-v-f3ef42d0] {\n  padding-left: 56px;\n  padding-top: 29px;\n}\n.refresh-table-button[data-v-f3ef42d0] {\n  display: flex;\n  width: 150px;\n  height: 32px;\n  background: #CBCBCB;\n  color: #212121;\n  border-radius: 4px;\n  opacity: 1;\n  border: none;\n  font-family: 'ROBOTO';\n  font-style: normal;\n  font-size: 14px;\n  font-weight: 500;\n  letter-spacing: 0.02px;\n  text-transform: uppercase;\n  padding-top: 6px;\n  padding-left: 12px;\n}\n\n/* table */\n.data-table[data-v-f3ef42d0] {\n  padding-top: 18px;\n  position: absolute;\n  width: 1304px;\n  height: 311px;\n}\n.table-header-text[data-v-f3ef42d0] {\n  padding-top: 11px;\n  color: #373737;\n  font-size: 13px;\n  font-family: \"Roboto\";\n  font-weight: 500;\n}\n.table-header[data-v-f3ef42d0] {\n  display: grid;\n  position: absolute;\n  width: 1304px;\n  height: 40px;\n  background: #e3e3e3 0% 0% no-repeat padding-box;\n  grid-template-columns: 423px 230px 223px 152px 200px;\n  z-index: 1;\n}\n.table-body[data-v-f3ef42d0] {\n  padding-top: 40px;\n  min-height: 291px;\n  max-height: 612px;\n  overflow-y: auto;\n  overflow-x: hidden;\n}\n.table-body[data-v-f3ef42d0]::-webkit-scrollbar {\n  display: none;\n}\n.table-row[data-v-f3ef42d0] {\n  /* display: grid; */\n  display: inline-block;\n  width: 1304px;\n  opacity: 1;\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  border: 1px solid #E3E3E3;\n  /* grid-template-columns: 423px 230px 219px 20px 136px 200px 39px; */\n}\n.table-row-text[data-v-f3ef42d0] {\n  padding-top: 13px;\n  padding-bottom: 13px;\n  color: #212121;\n  font-size: 14px;\n  font-family: \"Roboto\";\n  font-weight: 300;\n}\n.download-artifact-text[data-v-f3ef42d0] {\n  color: #616161;\n  font-size: 14px;\n  font-family: \"Roboto\";\n  font-weight: 300;\n}\n.row-down-icon[data-v-f3ef42d0] {\n  width: 20px;\n  display: flex;\n  cursor: pointer;\n}\n.icon-container[data-v-f3ef42d0] {\n  margin: 30px 20px 30px 30px;\n  width: 60px;\n  height: 60px;\n  border: 1px solid #ECEFF1;\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  border-radius: 50%;\n  opacity: 1;\n  display: flex;\n}\n.icon-container.small[data-v-f3ef42d0] {\n  width: 18px;\n  height: 18px;\n  margin: 0px;\n  margin-left: 36px;\n}\n.content-header-text-container[data-v-f3ef42d0] {\n  padding: 30px;\n  display: flex;\n  justify-content: flex-end;\n  flex-direction: column;\n}\n\n/* #top-label, #bottom-label {\n    position:absolute;\n} */\n#top-label[data-v-f3ef42d0] {\n\n  display: flex;\n  flex-direction: row;\n  text-align: left;\n  font-style: normal;\n  font-weight: 500;\n  font-size: 24px;\n  font-family: Roboto;\n  letter-spacing: 0px;\n  color: #212121;\n  opacity: 1;\n}\n#bottom-label[data-v-f3ef42d0] {\n  font-style: normal;\n  font-weight: 500;\n  font-size: 20px;\n  font-family: Roboto;\n  text-align: left;\n  letter-spacing: 0px;\n  color: #757575;\n  opacity: 1;\n}\n.row-information-container[data-v-f3ef42d0] {\n  display: block;\n  height: auto;\n  width: 1304px;\n}\n.table-row-status-icon[data-v-f3ef42d0] {\n  height: 13px;\n  width: 13px;\n}\n.table-row-status[data-v-f3ef42d0] {\n  margin-top: 12px;\n}\n.mdi-timer-sand[data-v-f3ef42d0] {\n  color: #A1A1A1;\n}\n.mdi-arrow-up-circle-outline[data-v-f3ef42d0] {\n  color: #449DD1;\n}\n.mdi-check-circle-outline[data-v-f3ef42d0] {\n  color: #149911;\n}\n.mdi-close-circle-outline[data-v-f3ef42d0] {\n  color: #DF2935;\n}\n.chevronIcon[data-v-f3ef42d0] {\n  cursor: pointer;\n}\n.profile-icon[data-v-f3ef42d0] {\n  color: #909090;\n}\n.dropdown[data-v-f3ef42d0] {\n  position: relative;\n  display: inline-block;\n}\n.artifactDownloadBtn[data-v-f3ef42d0] {\n  display: inline-flex;\n  background-color: #151233;\n  width: 138px;\n  height: 32px;\n  border-radius: 4px;\n  opacity: 1;\n  color: white;\n}\n.dropdown-content[data-v-f3ef42d0] {\n  display: block;\n  position: absolute;\n  background-color: #FFFFFF;\n  min-width: 136px;\n  height: 32px;\n  z-index: 1;\n  left: 234px;\n  border: 1px solid #A1A1A1;\n  box-shadow: 2px 2px 4px #00000029;\n}\n.dropdown-content span[data-v-f3ef42d0] {\n  height: 30px;\n  width: 134px;\n  display: block;\n  padding-left: 10px;\n  padding-top: 2px;\n  cursor: pointer;\n}\n.dropdown-content span[data-v-f3ef42d0]:hover {\n  background-color: #ECEFF1;\n}\n.artifactIcons[data-v-f3ef42d0] {\n  padding-right: 8px;\n  cursor: pointer;\n}\n.mdi-monitor[data-v-f3ef42d0]::before {\n  font-size: 18px;\n  line-height: initial;\n}\n.mdi-apple-ios[data-v-f3ef42d0]::before {\n  font-size: 18px;\n  line-height: initial;\n}\n.mdi-android[data-v-f3ef42d0]::before {\n  font-size: 18px;\n  line-height: initial;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/publish/publishHistory.vue"],"names":[],"mappings":";AAgQA;EACE,sBAAsB;EACtB,aAAa;EACb,gBAAgB;EAChB,kBAAkB;EAClB,aAAa;EACb,aAAa;EACb,kBAAkB;EAClB,+CAA+C;EAC/C,yBAAyB;EACzB,kBAAkB;EAClB,UAAU;AACZ;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,aAAa;EACb,aAAa;EACb,+CAA+C;EAC/C,yBAAyB;EACzB,kBAAkB;EAClB,UAAU;AACZ;AAEA;EACE,iBAAiB;EACjB,UAAU;EACV,eAAe;AACjB;AAGA;EACE,gBAAgB;EAChB,gBAAgB;EAChB,2CAA2C;EAC3C,mBAAmB;EACnB,cAAc;EACd,UAAU;AACZ;AAEA;EACE,kBAAkB;EAClB,kBAAkB;EAClB,kBAAkB;EAClB,gBAAgB;;EAEhB,mBAAmB;EACnB,iBAAiB;EACjB,cAAc;EACd,gBAAgB;EAChB,UAAU;AACZ;;AAEA,QAAQ;AACR;EACE,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;AACrB;AAEA;EACE,UAAU;EACV,eAAe;EACf,WAAW;EACX,YAAY;EACZ,eAAe;AACjB;AAEA;EACE,gBAAgB;EAChB,2CAA2C;EAC3C,gBAAgB;EAChB,mBAAmB;EACnB,cAAc;EACd,UAAU;EACV,kBAAkB;AACpB;AAEA;EACE,cAAc;EACd,oBAAoB;EACpB,4BAA4B;AAC9B;;AAEA,gBAAgB;AAChB;EACE,kBAAkB;EAClB,iBAAiB;AACnB;AAEA;EACE,aAAa;EACb,YAAY;EACZ,YAAY;EACZ,mBAAmB;EACnB,cAAc;EACd,kBAAkB;EAClB,UAAU;EACV,YAAY;EACZ,qBAAqB;EACrB,kBAAkB;EAClB,eAAe;EACf,gBAAgB;EAChB,sBAAsB;EACtB,yBAAyB;EACzB,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA,UAAU;AACV;EACE,iBAAiB;EACjB,kBAAkB;EAClB,aAAa;EACb,aAAa;AACf;AAEA;EACE,iBAAiB;EACjB,cAAc;EACd,eAAe;EACf,qBAAqB;EACrB,gBAAgB;AAClB;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,aAAa;EACb,YAAY;EACZ,+CAA+C;EAC/C,oDAAoD;EACpD,UAAU;AACZ;AAEA;EACE,iBAAiB;EACjB,iBAAiB;EACjB,iBAAiB;EACjB,gBAAgB;EAChB,kBAAkB;AACpB;AAEA;EACE,aAAa;AACf;AAEA;EACE,mBAAmB;EACnB,qBAAqB;EACrB,aAAa;EACb,UAAU;EACV,+CAA+C;EAC/C,yBAAyB;EACzB,oEAAoE;AACtE;AAEA;EACE,iBAAiB;EACjB,oBAAoB;EACpB,cAAc;EACd,eAAe;EACf,qBAAqB;EACrB,gBAAgB;AAClB;AAEA;EACE,cAAc;EACd,eAAe;EACf,qBAAqB;EACrB,gBAAgB;AAClB;AAEA;EACE,WAAW;EACX,aAAa;EACb,eAAe;AACjB;AAEA;EACE,2BAA2B;EAC3B,WAAW;EACX,YAAY;EACZ,yBAAyB;EACzB,+CAA+C;EAC/C,kBAAkB;EAClB,UAAU;EACV,aAAa;AACf;AAEA;EACE,WAAW;EACX,YAAY;EACZ,WAAW;EACX,iBAAiB;AACnB;AAEA;EACE,aAAa;EACb,aAAa;EACb,yBAAyB;EACzB,sBAAsB;AACxB;;AAEA;;GAEG;AACH;;EAEE,aAAa;EACb,mBAAmB;EACnB,gBAAgB;EAChB,kBAAkB;EAClB,gBAAgB;EAChB,eAAe;EACf,mBAAmB;EACnB,mBAAmB;EACnB,cAAc;EACd,UAAU;AACZ;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,eAAe;EACf,mBAAmB;EACnB,gBAAgB;EAChB,mBAAmB;EACnB,cAAc;EACd,UAAU;AACZ;AAEA;EACE,cAAc;EACd,YAAY;EACZ,aAAa;AACf;AAEA;EACE,YAAY;EACZ,WAAW;AACb;AAEA;EACE,gBAAgB;AAClB;AAEA;EACE,cAAc;AAChB;AAEA;EACE,cAAc;AAChB;AAEA;EACE,cAAc;AAChB;AAEA;EACE,cAAc;AAChB;AAEA;EACE,eAAe;AACjB;AAEA;EACE,cAAc;AAChB;AAEA;EACE,kBAAkB;EAClB,qBAAqB;AACvB;AAEA;EACE,oBAAoB;EACpB,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,UAAU;EACV,YAAY;AACd;AAEA;EACE,cAAc;EACd,kBAAkB;EAClB,yBAAyB;EACzB,gBAAgB;EAChB,YAAY;EACZ,UAAU;EACV,WAAW;EACX,yBAAyB;EACzB,iCAAiC;AACnC;AAEA;EACE,YAAY;EACZ,YAAY;EACZ,cAAc;EACd,kBAAkB;EAClB,gBAAgB;EAChB,eAAe;AACjB;AAEA;EACE,yBAAyB;AAC3B;AAEA;EACE,kBAAkB;EAClB,eAAe;AACjB;AAEA;EACE,eAAe;EACf,oBAAoB;AACtB;AAEA;EACE,eAAe;EACf,oBAAoB;AACtB;AAEA;EACE,eAAe;EACf,oBAAoB;AACtB","sourcesContent":["<script setup lang=\"ts\">\nimport { ref, reactive, watch } from \"vue\";\nimport RowInformationContainer from \"./components/rowInformationContainer.vue\"\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IQCloudApi } from \"../../../../../domain/infrastructure/IQCloudApi\";\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport { IExportJobHistoryDbItem, IPlatformSelection, StatusType } from \"../../../../../../../common/qCloudTemp/exporter\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport { IExportJobArtifactInfoItem } from \"../../../../../domain/model/models\";\nimport { IConfig } from \"../../../../../domain/infrastructure/IConfig\";\nimport { INetwork } from \"../../../../../domain/infrastructure/INetwork\";\n\ninterface Step {\n  ID: string;\n  name: string;\n  status: StatusType;\n}\ninterface PublishHistoryTableRow {\n  title: string;\n  createDate: string;\n  createdBy: string;\n  status: string;\n  platform: IPlatformSelection;\n  steps: Array<Step>;\n  showInformation: boolean;\n  isMultiStepJob: boolean;\n  cloudProvider: IExportJobHistoryDbItem[\"cloudProvider\"];\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst config = container.resolve<IConfig>(IConfig);\nconst network = container.resolve<INetwork>(INetwork);\nconst qcloudApi = container.resolve<IQCloudApi>(IQCloudApi);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst showingTab = ref(<\"publish\" | \"download\">(\"publish\"));\nconst tableData = ref<Array<PublishHistoryTableRow>>();\nconst tableHeaders = [\n  { name: \"Title\", style: \"padding-left:20px\" },\n  { name: \"Create Date\", style: \"\" },\n  { name: \"Created By\", style: \"\" },\n  { name: \"Status\", style: \"\" },\n  { name: \"Platform\", style: \"\" },\n];\nconst artifactListVisible = ref(false)\nconst artifactsMap = reactive(new Map<string, Array<IExportJobArtifactInfoItem>>());\n\nwatch(() => showingTab.value, (_new, _old) => {\n  getApplicationJobHistory();\n})\n\n\nconst getApplicationJobHistory = async () => {\n  executor.execute(\n    async () => {\n      let historyList: Array<IExportJobHistoryDbItem> = await qcloudApi.listApplicationJobHistory({ applicationID: viewModel.studio.appId, type: showingTab.value });\n      let openedRows: Map<string, Array<Step>> = new Map();\n\n      let currentlyOpenedRows = tableData.value?.filter(e => e.showInformation);\n      if (currentlyOpenedRows)\n        for await (const e of currentlyOpenedRows) {\n          let steps = await getListExportJobSteps(e.title, e.isMultiStepJob);\n          openedRows.set(e.title, steps)\n        }\n\n      tableData.value = historyList.map(e => {\n        const isRowOpened = openedRows.has(e.ID);\n\n        return {\n          title: e.ID,\n          createDate: dateFormat(new Date(e.createDate)),\n          createdBy: e.createdBy,\n          status: e.status,\n          platform: e.status == \"success\" ? e.platform : undefined,\n          steps: isRowOpened ? openedRows.get(e.ID) : [],\n          showInformation: isRowOpened,\n          isMultiStepJob: e.isMultiStep,\n          cloudProvider: e.cloudProvider\n        } as PublishHistoryTableRow\n      })\n    },\n    { loading: true }\n  );\n}\n\ngetApplicationJobHistory();\n\nfunction dateFormat(date: Date) {\n  return `${date.toLocaleDateString(\"en-US\", {day: \"2-digit\", month: \"2-digit\", year: \"numeric\"}).replace(/\\//g, \".\")}, ${date.toLocaleString(\"en-US\", {second: \"2-digit\", minute: \"2-digit\", hour: \"2-digit\", hourCycle: \"h24\"})}`;\n}\n\nasync function openCloseRow(row: PublishHistoryTableRow) {\n  if (row.status == \"waiting\")\n    return\n\n  if (row.showInformation) {\n    row.showInformation = false;\n    return;\n  }\n\n  executor.execute(\n    async () => {\n      row.steps = await getListExportJobSteps(row.title, row.isMultiStepJob);\n      if (showingTab.value == \"download\" && row.status == \"success\") {\n        await getArtifacts(row);\n      }\n    },\n    { loading: true }\n  );\n\n  row.showInformation = !row.showInformation;\n}\n\nasync function getListExportJobSteps(ID: string, isMultiStep: boolean) {\n  let jobSteps = await qcloudApi.listExportJobSteps(ID, isMultiStep);\n  return jobSteps.map(e => {\n    return {\n      ID: e.ID,\n      name: e.name,\n      status: e.status\n    } as Step\n  });\n}\n\nasync function refreshTable() {\n  await getApplicationJobHistory();\n}\n\n// download artifacts\nasync function getArtifacts(row: PublishHistoryTableRow) {\n  if (!artifactsMap.has(row.title)) {\n    const artifacts = await qcloudApi.listExportJobArtifacts(row.title);\n    if (artifacts && artifacts.length > 0) {\n      artifactsMap.set(row.title, artifacts);\n    }\n  }\n\n}\n\nfunction computedArtifact(type: string) {\n  switch (type) {\n    case 'web':\n      return 'mdi mdi-monitor artifactIcons';\n    case 'ios':\n      return 'mdi mdi-apple-ios artifactIcons';\n    case 'android':\n      return 'mdi mdi-android artifactIcons';\n    default:\n      break;\n  }\n}\n\nfunction getArtifactLink(artifact: IExportJobArtifactInfoItem) {\n  const token = viewModel.user!.authorization;\n  const { objectName } = artifact.details;\n  const url = config.getValue(\"providerApiUrl\");\n  return network.join(url, `/minio/getExportJobArtifact?objectName=${objectName}&t=${token}`);\n}\n\n\nfunction downloadArtifact(artifact: IExportJobArtifactInfoItem) {\n  const a = document.createElement(\"a\");\n  a.href = getArtifactLink(artifact);\n  a.target = \"new\";\n  a.download = \"download\";\n  document.body.appendChild(a);\n  a.click();\n  document.body.removeChild(a);\n}\n\nasync function showArtifactsBtnClicked(row: PublishHistoryTableRow) {\n  await getArtifacts(row);\n  artifactListVisible.value = !artifactListVisible.value;\n}\n</script>\n\n<template>\n  <div class=\"content-header\">\n    <!-- <div class=\"icon-container\">\n      <img src=\"../../../../../../src/images/is-bankasi-logo.jpg\" alt=\"is-bankasi-logo\" class=\"header-logo\" width=\"57\"\n        height=\"57\" />\n    </div> -->\n    <div class=\"content-header-text-container\">\n      <label id=\"top-label\">{{ viewModel.studio.appName }}</label>\n      <label id=\"bottom-label\">You can see application {{ showingTab }} history in here.</label>\n    </div>\n\n  </div>\n  <div class=\"tabs\">\n    <div class=\"bar\">\n      <input type=\"radio\" id=\"publishJobTab\" name=\"bar\" :checked=\"showingTab == 'publish'\"\n        @change=\"showingTab = 'publish'\" />\n      <label for=\"publishJobTab\">Publish Jobs</label>\n\n      <input type=\"radio\" id=\"downloadsTab\" name=\"bar\" :checked=\"showingTab == 'download'\"\n        @change=\"showingTab = 'download'\" />\n      <label for=\"downloadsTab\">Downloads</label>\n    </div>\n    <div class=\"tab-content\">\n      <button class='refresh-table-button' @click=\"refreshTable\">\n        <i class=\"mdi mdi-refresh\" style=\"margin-right: 3px;\"></i>\n        Refresh Table\n      </button>\n      <div class=\"data-table\">\n        <div class=\"table-header\">\n          <span v-for=\"header in tableHeaders\" :class=\"('table-header-text')\" :style=\"header.style\"> {{ header.name }}\n          </span>\n        </div>\n        <div class=\"table-body\">\n          <div class=\"table-row\" v-for=\"row in tableData\">\n            <span class='table-row-text' style=\"margin-left: 20px;width: 403px;float: left;\">{{ row.title }}</span>\n            <span class='table-row-text' style=\"width: 228px; float: left;\">{{ row.createDate }}</span>\n            <span class='table-row-text' style=\"width: 220px; float: left;\">{{ row.createdBy }}</span>\n            <span class=\"table-row-status\" style=\"float: left; width: 20px;\">\n              <i class=\"mdi mdi-timer-sand table-row-status-icon\" v-if=\"row.status == 'waiting'\"></i>\n              <i class=\"mdi mdi-arrow-up-circle-outline table-row-status-icon\" v-else-if=\"row.status == 'running'\"></i>\n              <i class=\"mdi mdi-check-circle-outline table-row-status-icon\" v-else-if=\"row.status == 'success'\"></i>\n              <i class=\"mdi mdi-close-circle-outline table-row-status-icon\" v-else-if=\"row.status == 'failed'\"></i>\n            </span>\n            <span class='table-row-text' style=\"float: left;width: 136px;\">{{ row.status }}</span>\n            <span class='table-row-text' style=\"float: left;width: 230px;\">\n              <i class=\"mdi mdi-monitor\" v-if=\"row.status == 'success' && row.platform?.web\"></i>\n              <i class=\"mdi mdi-apple-ios\" style=\"padding-left: 8px;\" v-if=\"row.status == 'success' && row.platform?.mobile\"></i>\n              <!-- <i class=\"mdi mdi-android\" v-else-if=\"row.status == 'success' && row.platform == 'android'\"></i> -->\n              <span v-if=\"row.status != 'success' || !row.platform || (!row.platform?.web && !row.platform?.mobile)\">-</span>\n            </span>\n            <span class='row-down-icon' @click=\"openCloseRow(row)\">\n              <i class=\"mdi mdi-chevron-down\" style=\"padding-top: 13px;\" v-if=\"!row.showInformation\"></i>\n              <i class=\"mdi mdi-chevron-up\" style=\"padding-top: 13px;\" v-else></i>\n            </span>\n            <div :id=\"row.title\" class=\"row-information-container\" v-if=\"row.showInformation\">\n              <RowInformationContainer v-for=\"step in row.steps\" :ID=\"step.ID\" :name=\"step.name\" :appID=\"viewModel.studio.appId\"\n                :status=\"step.status\" :groupID=\"row.title\" :cloudProvider=\"row.cloudProvider\" />\n              <div v-if=\"row.status == 'success' && showingTab == 'download'\">\n                <hr style=\"margin: 0px 0px 0px 20px; width: 1264px;\" />\n                <div\n                  style=\"height: 88px; height: 88px;float: right;padding-right: 44px;padding-top: 28px; position: relative;display: inline-block;\">\n                  <span class=\"download-artifact-text\">You can download this jobs artifacts. </span>\n                  <button @click=\"showArtifactsBtnClicked(row)\" class=\"artifactDownloadBtn\"><i\n                      class='mdi mdi-tray-arrow-down' style=\"padding-right: 8px;\"></i>DOWNLOAD <i\n                      class='mdi mdi-chevron-down'></i></button>\n                  <div v-if=\"artifactListVisible\" v-for=\"artifact in artifactsMap.get(row.title)\"\n                    class=\"dropdown-content\">\n                    <span @click=\"downloadArtifact(artifact)\" class='artifactListItem'><i\n                        :class=\"computedArtifact(artifact.type)\"></i>{{ artifact.type\n                      }}</span>\n                  </div>\n                </div>\n              </div>\n            </div>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n<style scoped>\n.content-header {\n  /* Layout Properties */\n  display: flex;\n  margin-top: 96px;\n  margin-left: 252px;\n  width: 1417px;\n  height: 120px;\n  /* UI Properties */\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  border: 1px solid #BCBCBC;\n  border-radius: 4px;\n  opacity: 1;\n}\n\n.tabs {\n  margin-top: 12px;\n  margin-left: 252px;\n  width: 1417px;\n  height: 828px;\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  border: 1px solid #BCBCBC;\n  border-radius: 4px;\n  opacity: 1;\n}\n\n.prev-button {\n  margin-left: 17px;\n  opacity: 1;\n  cursor: pointer;\n}\n\n\n.app-name {\n  margin-left: 9px;\n  text-align: left;\n  font: normal normal normal 15px/20px Roboto;\n  letter-spacing: 0px;\n  color: #616161;\n  opacity: 1;\n}\n\n.progress-details-text {\n  /* UI Properties */\n  padding-left: 12px;\n  font-style: normal;\n  font-weight: 500;\n  ;\n  font-family: Roboto;\n  letter-spacing: 0;\n  color: #212121;\n  text-align: left;\n  opacity: 1;\n}\n\n/* tab */\n.bar {\n  margin-top: 32px;\n  margin-left: 32px;\n  margin-bottom: 20px;\n}\n\n.bar input[type=\"radio\"] {\n  opacity: 0;\n  position: fixed;\n  width: 93px;\n  height: 21px;\n  cursor: pointer;\n}\n\n.bar label {\n  text-align: left;\n  font: normal normal medium 16px/21px Roboto;\n  font-weight: 500;\n  letter-spacing: 0px;\n  color: #A1A1A1;\n  opacity: 1;\n  margin-right: 28px;\n}\n\n.bar input[type=\"radio\"]:checked+label {\n  color: #151233;\n  border-bottom: solid;\n  border-bottom-color: #151233;\n}\n\n/* tab content */\n.tab-content {\n  padding-left: 56px;\n  padding-top: 29px;\n}\n\n.refresh-table-button {\n  display: flex;\n  width: 150px;\n  height: 32px;\n  background: #CBCBCB;\n  color: #212121;\n  border-radius: 4px;\n  opacity: 1;\n  border: none;\n  font-family: 'ROBOTO';\n  font-style: normal;\n  font-si