ign: left;\n  font-style: normal;\n  font-weight: 500;\n  font-size: 24px;\n  font-family: Roboto;\n  letter-spacing: 0px;\n  color: #212121;\n  opacity: 1;\n}\n\n#bottom-label {\n  font-style: normal;\n  font-weight: 500;\n  font-size: 20px;\n  font-family: Roboto;\n  text-align: left;\n  letter-spacing: 0px;\n  color: #757575;\n  opacity: 1;\n}\n\n.row-information-container {\n  display: block;\n  height: auto;\n  width: 1304px;\n}\n\n.table-row-status-icon {\n  height: 13px;\n  width: 13px;\n}\n\n.table-row-status {\n  margin-top: 12px;\n}\n\n.mdi-timer-sand {\n  color: #A1A1A1;\n}\n\n.mdi-arrow-up-circle-outline {\n  color: #449DD1;\n}\n\n.mdi-check-circle-outline {\n  color: #149911;\n}\n\n.mdi-close-circle-outline {\n  color: #DF2935;\n}\n\n.chevronIcon {\n  cursor: pointer;\n}\n\n.profile-icon {\n  color: #909090;\n}\n\n.dropdown {\n  position: relative;\n  display: inline-block;\n}\n\n.artifactDownloadBtn {\n  display: inline-flex;\n  background-color: #151233;\n  width: 138px;\n  height: 32px;\n  border-radius: 4px;\n  opacity: 1;\n  color: white;\n}\n\n.dropdown-content {\n  display: block;\n  position: absolute;\n  background-color: #FFFFFF;\n  min-width: 136px;\n  height: 32px;\n  z-index: 1;\n  left: 234px;\n  border: 1px solid #A1A1A1;\n  box-shadow: 2px 2px 4px #00000029;\n}\n\n.dropdown-content span {\n  height: 30px;\n  width: 134px;\n  display: block;\n  padding-left: 10px;\n  padding-top: 2px;\n  cursor: pointer;\n}\n\n.dropdown-content span:hover {\n  background-color: #ECEFF1;\n}\n\n.artifactIcons {\n  padding-right: 8px;\n  cursor: pointer;\n}\n\n.mdi-monitor::before {\n  font-size: 18px;\n  line-height: initial;\n}\n\n.mdi-apple-ios::before {\n  font-size: 18px;\n  line-height: initial;\n}\n\n.mdi-android::before {\n  font-size: 18px;\n  line-height: initial;\n}\n</style>\n"],"sourceRoot":""}]);
