async function changeShowLog() {
  if (props.status == "waiting")
    return

  if (!showLog.value) {
    await executor.execute(
      async () => {
        let resp: IlistExportJobStepLogsResponse = await qcloudApi.listExportJobStepLogs(props.ID);
        if (resp.logs) {
          logs.value = resp.logs
        }
      },
      { loading: true }
    );
  }

  if (props.name == "Deployment Completed"){
    hosts.value = props.cloudProvider == "azure" ?  await getAzureDeploymentResult() : await getDeploymentResult();
  }

  showLog.value = !showLog.value
}

function calculateRowHeight() {
  if (!showLog.value)
    return "40px";

  switch (props.name) {
    case "Pipeline Started":
      return "109px";
    case "Deployment Completed":
      return "152px";
    default:
      return "126px";
  }
}

function getFullUrl(url: string, type: DeployType) {
  if (type == "application")
    return `http://${url}`;

  return `http://${url}/swagger-ui.html#/`
}

async function getAzureDeploymentResult() {
  let hosts = Array<IDeployedPackageUrl>();
  await executor.execute(async () => {
    const application = await qcloudApi.getApplication(props.appID);
    hosts.push({url: application.cloud?.azure?.dnsEndpoint!, name: application.name, "type": "application"})
  }, { loading: true });
    
  return hosts;
}
async function getDeploymentResult() {
  //FIXME: this information must be get from application
  let hosts = Array<IDeployedPackageUrl>();
  await executor.execute(async () => {
    const jobs: Array<IQcloudJob> = await qcloudApi.getExportJobsByGroupId({ groupID: props.groupID }).then(res => res.jobs);
    const deploymentJobs = jobs.filter(e => e.layer == 2);
    if (deploymentJobs.length <= 0)
      return;

    const _hosts = (deploymentJobs[0].jobData as IDeployJobData).deploymentResult?.hosts;

    hosts.push({name:'ui', type: 'application', url:`http://${_hosts?.find(e => e.startsWith('ui.'))!}`  })

    jobs.filter(e => e.layer == 1).map(e => (e.jobData as IBuildJobData).package).map(e => {
      let hostStartStr = PlateauHelper.getApplicationUINameFromQuiPkg(e.mainDir);
      let url = _hosts?.find(h => h.startsWith(hostStartStr));
      if (!url)
        return;
      url = getFullUrl(url, e.type);
      hosts.push({
        name: e.name,
        type: e.type,
        url
      } as IDeployedPackageUrl)
    });


  }, { loading: true })
  return hosts;
}

