AY;EACZ,YAAY;EACZ,cAAc;AAChB;AAEA;EACE,aAAa;AACf;AAEA;EACE,cAAc;EACd,YAAY;EACZ,kBAAkB;EAClB,gBAAgB;EAChB,sBAAsB;AACxB;AAEA;EACE,qBAAqB;EACrB,aAAa;EACb,YAAY;EACZ,eAAe;EACf,kBAAkB;EAClB,iBAAiB;AACnB;AAEA;EACE,aAAa;EACb,eAAe;EACf,kBAAkB;EAClB,iBAAiB;AACnB;AAEA;EACE,cAAc;EACd,YAAY;EACZ,yCAAyC;EACzC,mBAAmB;EACnB,cAAc;EACd,kBAAkB;AACpB;AAGA;EACE,cAAc;AAChB;AAEA;EACE,cAAc;AAChB;AAEA;EACE,cAAc;AAChB;AAEA;EACE,cAAc;AAChB","sourcesContent":["<script setup lang=\"ts\">\nimport { ref } from 'vue';\nimport { IlistExportJobStepLogsResponse, IQCloudApi } from '../../../../../../domain/infrastructure/IQCloudApi';\nimport { container } from \"../../../../../../domain/core/diContainer\";\nimport { DeployType, IBuildJobData, IDeployJobData, IExportJobHistoryDbItem, IQcloudJob, StatusType } from '../../../../../../../../common/qCloudTemp/exporter';\nimport { IUseCaseExecutor } from '../../../../../../domain/useCase/IUseCaseExecutor';\nimport { PlateauHelper } from '@stechquick/algae/lib/helpers/plateau/plateauHelper';\n\ninterface IStep {\n  ID: string;\n  name: string;\n  status: StatusType;\n  groupID: string;\n  appID: string;\n  cloudProvider: IExportJobHistoryDbItem[\"cloudProvider\"];\n}\n\ninterface IDeployedPackageUrl {\n  name: string,\n  type: DeployType,\n  url: string\n}\n\nconst qcloudApi = container.resolve<IQCloudApi>(IQCloudApi);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst showLog = ref(false);\nconst logs = ref(Array<string>());\nconst props = defineProps<IStep>();\nconst hosts = ref(Array<IDeployedPackageUrl>());\nconst customSteps = [\"Pipeline Started\", \"Deployment Completed\"]\n\nasync function changeShowLog() {\n  if (props.status == \"waiting\")\n    return\n\n  if (!showLog.value) {\n    await executor.execute(\n      async () => {\n        let resp: IlistExportJobStepLogsResponse = await qcloudApi.listExportJobStepLogs(props.ID);\n        if (resp.logs) {\n          logs.value = resp.logs\n        }\n      },\n      { loading: true }\n    );\n  }\n\n  if (props.name == \"Deployment Completed\"){\n    hosts.value = props.cloudProvider == \"azure\" ?  await getAzureDeploymentResult() : await getDeploymentResult();\n  }\n\n  showLog.value = !showLog.value\n}\n\nfunction calculateRowHeight() {\n  if (!showLog.value)\n    return \"40px\";\n\n  switch (props.name) {\n    case \"Pipeline Started\":\n      return \"109px\";\n    case \"Deployment Completed\":\n      return \"152px\";\n    default:\n      return \"126px\";\n  }\n}\n\nfunction getFullUrl(url: string, type: DeployType) {\n  if (type == \"application\")\n    return `http://${url}`;\n\n  return `http://${url}/swagger-ui.html#/`\n}\n\nasync function getAzureDeploymentResult() {\n  let hosts = Array<IDeployedPackageUrl>();\n  await executor.execute(async () => {\n    const application = await qcloudApi.getApplication(props.appID);\n    hosts.push({url: application.cloud?.azure?.dnsEndpoint!, name: application.name, \"type\": \"application\"})\n  }, { loading: true });\n    \n  return hosts;\n}\nasync function getDeploymentResult() {\n  //FIXME: this information must be get from application\n  let hosts = Array<IDeployedPackageUrl>();\n  await executor.execute(async () => {\n    const jobs: Array<IQcloudJob> = await qcloudApi.getExportJobsByGroupId({ groupID: props.groupID }).then(res => res.jobs);\n    const deploymentJobs = jobs.filter(e => e.layer == 2);\n    if (deploymentJobs.length <= 0)\n      return;\n\n    const _hosts = (deploymentJobs[0].jobData as IDeployJobData).deploymentResult?.hosts;\n\n    hosts.push({name:'ui', type: 'application', url:`http://${_hosts?.find(e => e.startsWith('ui.'))!}`  })\n\n    jobs.filter(e => e.layer == 1).map(e => (e.jobData as IBuildJobData).package).map(e => {\n      let hostStartStr = PlateauHelper.getApplicationUINameFromQuiPkg(e.mainDir);\n      let url = _hosts?.find(h => h.startsWith(hostStartStr));\n      if (!url)\n        return;\n      url = getFullUrl(url, e.type);\n      hosts.push({\n        name: e.name,\n        type: e.type,\n        url\n      } as IDeployedPackageUrl)\n    });\n\n\n  }, { loading: true })\n  return hosts;\n}\n</script>\n\n<template>\n  <div class=\"step-row\" :style=\"{ background: (showLog ? '#EEEEEE' : '#FFFFFF'), height: calculateRowHeight() }\">\n    <span class=\"step-icon\" @click=\"changeShowLog()\">\n      <i id=\"play-icon\" class=\"mdi mdi-play\" v-if=\"!showLog\"></i>\n      <i id=\"play-icon\" class=\"mdi mdi-menu-down\" v-else></i>\n    </span>\n    <span style=\"margin-right: 4px;\">\n      <i class=\"mdi mdi-timer-sand\" v-if=\"props.status == 'waiting'\"></i>\n      <i class=\"mdi mdi-arrow-up-circle-outline\" v-else-if=\"props.status == 'running'\"></i>\n      <i class=\"mdi mdi-check-circle-outline\" v-else-if=\"props.status == 'success'\"></i>\n      <i class=\"mdi mdi-close-circle-outline\" v-else-if=\"props.status == 'failed'\"></i>\n    </span>\n    <span>{{ props.name }}</span>\n    <div style=\"background-color: white; margin-top: 8px;\">\n      <div class=\"log-container\" v-if=\"showLog && !customSteps.includes(props.name)\">\n        <span class=\"log-text\" v-for=\"log in logs\">{{ log }}</span>\n      </div>\n    </div>\n    <div class=\"pipeline-started-container\" v-if=\"showLog && props.name == 'Pipeline Started'\">\n      <span  class=\"pipeline-started-text\">\n        You can see the pipeline status by going to this link.\n        <a href=\"http://jenkins.rally.softtech/job/deploy-gke-V2-3-0/\" target=\"_blank\" rel=\"noreferrer\"> GO TO LINK</a>\n      </span>\n    </div>\n    <div class=\"deployment-completed-container\" v-if=\"showLog && props.name == 'Deployment Completed'\">\n      <tbody style=\"display:inline-block; padding-left:30px;\">\n        <tr v-for=\"host in hosts\">\n          <td style=\"padding-top: 12px;\">\n            <a style=\"color: #151233; font-size: medium; font-weight: 500; text-decoration: none;\" :href=\"host.url\" target=\"_blank\" rel=\"noreferrer\">\n              {{ host.name.toLocaleUpperCase() }} {{ host.type== \"application\" ? \"APPLICATION\" : \"SERVICE\" }} URL <i class=\"mdi mdi-open-in-new\"></i>\n            </a>\n          </td>\n        </tr>\n      </tbody>\n    </div>\n  </div>\n</template>\n<style scoped>\n.step-row {\n  display: inline-block;\n  width: 1302px;\n  height: 40px;\n  padding-top: 8px;\n  padding-bottom: 8px;\n  /* UI Properties */\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  opacity: 1;\n}\n\n.step-icon {\n  margin-left: 20px;\n  width: 32px;\n  float: left;\n}\n\n#play-icon {\n  color: #BCBCBC;\n  padding-top: 12px;\n  padding-bottom: 12px;\n  cursor: pointer;\n}\n\n.log-container {\n  display: block;\n  margin-left: 52px;\n  height: 88px;\n  width: 883px;\n  overflow: auto;\n}\n\n.log-container::-webkit-scrollbar {\n  display: none;\n}\n\n.log-text {\n  display: block;\n  width: 883px;\n  font-size: smaller;\n  font-weight: 600;\n  font-family: monospace;\n}\n\n.pipeline-started-container {\n  align-content: center;\n  display: grid;\n  height: 69px;\n  margin-top: 8px;\n  padding-left: 20px;\n  background: white;\n}\n\n.deployment-completed-container {\n  height: 114px;\n  margin-top: 8px;\n  padding-left: 20px;\n  background: white;\n}\n\n.pipeline-started-text {\n  display: block;\n  width: 883px;\n  font: normal normal normal 14/20px Roboto;\n  letter-spacing: 0px;\n  color: #616161;\n  padding-left: 30px;\n}\n\n\n.mdi-timer-sand {\n  color: #A1A1A1;\n}\n\n.mdi-arrow-up-circle-outline {\n  color: #449DD1;\n}\n\n.mdi-check-circle-outline {\n  color: #149911;\n}\n\n.mdi-close-circle-outline {\n  color: #DF2935;\n}\n</style>\n"],"sourceRoot":""}]);
